<template>
  <div>
    <div v-show="showCompetition">
      <div class="top">
        <div class="title">
          <span class="left-title"></span>
          <span class="right-title"></span>
          <div class="description"></div>
        </div>
      </div>
      <div class="center">
        <div class="header">
          <el-card class="left-header-card">
            <div class="title"></div>
            <div class="tip">
              {{ time }}
            </div>
          </el-card>
          <el-card class="right-header-card">
            <div class="prize"></div>
            <div class="question" @click="ToQuestionCompetition()">
              
            </div>
            <div class="apply"></div>
          </el-card>
        </div>
        <div class="bottom">
          <el-card id="zoom" class="left-bottom-card">
            <div style="display: flex">
              <img src="@/assets/competitionIndex/map.png" alt="" />
              <div class="left-bottom-title">
                <span style="font-size: 1vw">()</span>
              </div>
            </div>

            <div v-if="rankGame.length != 0">
              <div class="bottom-rank" v-for="(item, index) in rankGame" :key="index">
                <span :class="index < 3 ? 'good' : ''">{{ index + 1 }}</span>
                {{item.major}}{{ item.year }}{{ item.username }} <span>{{item.score}}</span>
              </div>
            </div>
            <div v-else>
              <el-empty description=""></el-empty>
            </div>
          </el-card>
          <el-card id="zoom" class="right-bottom-card">
            <div class="right-bottom-title">
              <span></span>
              <span class="cooperation" @click="CompetitionCooperation"
                ></span
              >
            </div>
            <div v-if="lastGame.length != 0">
              <el-descriptions
                :colon="false"
                style="margin-top: 2vh; font-size: 2vh"
              >
                <el-descriptions-item label=""></el-descriptions-item>
                <el-descriptions-item label=""></el-descriptions-item>
                <el-descriptions-item label=""></el-descriptions-item>
              </el-descriptions>
              <el-descriptions
                :colon="false"
                v-for="(item, index) in 1"
                :key="index"
                style="margin-top: 2vh"
              >
                <el-descriptions-item
                  label=""
                ></el-descriptions-item>
                <el-descriptions-item
                  label="2024-1-17 "
                ></el-descriptions-item>
                <el-descriptions-item label=""></el-descriptions-item>
              </el-descriptions>
              <el-pagination
                :total="total"
                :small="true"
                style="margin: auto; bottom: 1vh; margin-top: 2vh"
                :hide-on-single-page="true"
              ></el-pagination>
            </div>
            <div v-else>
              <el-empty description=""></el-empty>
            </div>
          </el-card>
        </div>
      </div>
    </div>
    <div>
      <el-empty
        description=""
        v-show="!showCompetition"
      ></el-empty>
    </div>
    <div class="footer">  </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      time: "",
      lastGame: [1],
      rankGame: [],
      total: 1,
      showCompetition: true,
    };
  },
  methods: {
    //
    GetCompetitionRanking() {
      this.$axios.get(`/question/GetCompetitionRanking`).then((res) => {
        this.rankGame = res.data.data
      });
    },
    //
    ToQuestionCompetition() {
      this.$router.push({
        path: "/QuestionCompetition",
      });
    },
    //
    CompetitionCooperation() {
      let id = localStorage.getItem("id");
      this.$axios.get(`/question/IfQuestionAdmin?userId=${id}`).then((res) => {
        if (res.data.code == "1") {
          this.$router.push("/QuestionBank/question/");
        } else {
          this.$prompt("", "")
            .then((value) => {
              this.$axios
                .get(
                  `/question/AddQuestionAdmin?key=${value.value}&userId=${id}`
                )
                .then((res) => {
                  if (res.data.code == "1") {
                    this.$router.push("/QuestionBank/question/");
                  } else {
                    this.$message.error({ message: "" });
                  }
                });
            })
            .catch(() => {});
        }
      });
    },
    //1280
    ListenUserNavigator() {
      window.addEventListener("resize", () => {
        let winWidth =
          window.innerWidth ||
          document.body.clientWidth ||
          document.documentElement.clientWidth;
        if (winWidth < 1280) {
          this.showCompetition = false;
        } else {
          this.showCompetition = true;
        }
      });
    },
  },
  mounted() {
    this.GetCompetitionRanking()
    this.ListenUserNavigator();
  },
};
</script>

<style lang="less" scoped>
.top {
  background-color: #2b2b2b;
  height: 40vh;
  .title {
    text-align: center;
    font-size: 5vh;
    .left-title {
      font-weight: bolder;
      -webkit-background-clip: text; /**/
      -webkit-text-stroke: 1px white; /**/
      -webkit-text-fill-color: transparent; /**/
      line-height: 30vh;
    }
    .right-title {
      color: white;
      margin-left: 1vh;
      opacity: 0.6;
      font-size: 4vh;
      font-family: SimHei; /**/
      font-weight: bolder; /**/
      fill: transparent; /**/
      stroke: white; /**/
      stroke-dasharray: 3;
    }
    .description {
      font-size: 2vh;
      color: white;
      opacity: 0.4;
    }
  }
}
.center {
  .header {
    display: flex;
    justify-content: center;
    margin-top: -3vh;
    .left-header-card {
      width: 25%;
      height: 20vh;
      border-radius: 1vh;
      .title {
        color: black;
        font-weight: bolder;
        font-size: 3vh;
        -webkit-animation: flicker 2s infinite;
      }
      @keyframes flicker {
        0% {
          opacity: 1;
        }
        50% {
          opacity: 0.5;
        }
        100% {
          opacity: 1;
        }
      }
      .tip {
        margin-top: 2vh;
        font-size: 2vh;
        text-indent: 2em;
      }

      &:hover {
        box-shadow: -4px -4px 10px black;
      }
    }
    .right-header-card {
      width: 20%;
      height: 20vh;
      border-radius: 1vh;
      margin-left: 3vh;
      .prize {
        text-align: center;
        margin: 1vh;
        font-family: ""; /**/
        font-size: 3vh; /**/
        font-weight: bolder; /**/
        -webkit-text-stroke: 1px black; /**/
        -webkit-text-fill-color: transparent; /**/
        cursor: pointer;
      }
      .question {
        margin: 1vh;
        text-align: center;
        font-size: 3vh; /**/
        color: #3366ff; /**/
        text-shadow: 0 8px 10px #6699ff; /**/
        font-weight: bolder; /**/
        text-align: center; /**/
        cursor: pointer;
      }
      .apply {
        text-align: center;
        margin: 1vh;
        font-family: ""; /**/
        font-size: 3vh; /**/
        font-weight: bolder; /**/
        background: -webkit-linear-gradient(
          top,
          #ff0000,
          #00ff00
        ); /**/
        /**/
        -webkit-background-clip: text; /**/
        -webkit-text-fill-color: transparent; /**/
        cursor: pointer;
      }
      &:hover {
        box-shadow: 4px 4px 10px black;
      }
    }
  }
  .bottom {
    display: flex;
    justify-content: center;
    margin-top: 2vh;
    img {
      width: 4vh;
      height: 3vh;
    }
    .left-bottom-card {
      width: 40%;
      border-radius: 1vh;
      border: none;
      .left-bottom-title {
        font-size: 3vh;
        color: #bdc3c7;
        margin-bottom: 1vh;
      }
      &:hover {
        box-shadow: -4px -4px 10px #c5c5c5;
      }
    }
    .right-bottom-card {
      width: 40%;
      border-radius: 1vh;
      margin-left: 3vh;
      border: none;
      position: relative;
      .right-bottom-title {
        font-size: 3vh;
        display: flex;
        justify-content: space-between;
        color: #595959;
        .cooperation {
          font-size: 1.5vh;
          color: blue;
          margin-top: 1.5vh;
          cursor: pointer;
        }
      }
      &:hover {
        box-shadow: 4px 4px 10px #c5c5c5;
      }
    }
    .bottom-rank {
      padding: 1.5vh;
      padding-top: 2vh;
      border-bottom: 1px solid rgba(0, 0, 0, 0.121);
      border-radius: 1vh;
      justify-content: space-around;
      display: flex;
      .good {
        color: red;
      }
    }
  }
  margin-bottom: 10vh;
}
.footer {
  position: fixed;
  color: #bdc3c7;
  font-size: 1vh;
  right: 2vh;
  bottom: 2vh;
}
</style>